import { cssSelector, GenerateStyles, style, css, cssVariableAsVar } from '@umetrics/sartorius-ui-base';

export const linkStyle: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                cursor: pointer;
                color: ${cssVariableAsVar('color_tertiary_5')};
                text-decoration: none;
                display: flex;
                flex-direction: row;
                align-items: center;
            }
            ${root}:hover {
                text-decoration: underline;
            }
            ${is('!onBlack')}:visited {
                color: ${cssVariableAsVar('color_tertiary_4')};
            }

            ::slotted([slot='link-icon-slot']) {
                margin-right: 7px;
            }

            ${is('onBlack')} {
                color: ${cssVariableAsVar('color_primary_2')};
            }

            ${is('onBlack')}:visited {
                color: ${cssVariableAsVar('color_primary_2')};
            }
        `,
    ];
};
