import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { linkStyle } from './linkStyle';
import { LinkProperties, LinkSlot } from './linkProperties';
import { version } from './metadata';

@defineElement({ tagName: 'link', styles: linkStyle, version })
export class Link extends BaseElement implements LinkProperties {
    // Public members
    @property({ type: String, reflect: true })
    url;
    @property({ type: Function, reflect: false })
    callback;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'on-black' })
    onBlack;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'open-separate' })
    openSeparate;

    // Private members
    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            onBlack: !!this.onBlack,
        });

        return html` <a
            href="${this.url ?? ''}"
            class="${rootClasses}"
            rel="noopener noreferrer"
            target="${this.openSeparate ? '_blank' : ''}"
            @click=${(e) => {
                if (this.callback) {
                    this.callback(e);
                    e.preventDefault();
                    e.stopPropagation();
                }
            }}
            ><slot name="${LinkSlot.ICON_SLOT}"></slot><slot></slot
        ></a>`;
    }
}
